<template>

  <div>

    <!-- Table Container Card -->
    <b-card
      no-body
      class="mb-0"
    >

      <div class="m-2">

        <!-- Table Top -->
        <b-row>

          <!-- Per Page -->
          <b-col
            cols="12"
            md="6"
            class="d-flex align-items-center justify-content-start mb-1 mb-md-0"
          >
            <label>Ver</label>
            <v-select
              v-model="perPage"
              :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
              :options="perPageOptions"
              :clearable="false"
              class="per-page-selector d-inline-block mx-50"
            />
            <label>Entradas</label>
          </b-col>

          <!-- Search -->
          <b-col
            cols="12"
            md="6"
          >
            <div class="d-flex align-items-center justify-content-end">
              <b-form-input
                v-model="filter"
                class="d-inline-block mr-1"
                placeholder="Buscar..."
              />
              <b-button
                variant="primary"
                @click="addInventoryEntries"
              >
                <span class="text-nowrap">Agregar</span>
              </b-button>
            </div>
          </b-col>
        </b-row>

      </div>

      <b-table
        v-if="!loading"
        ref="refInventoryListTable"
        class="position-relative animated fadeIn"
        :items="items"
        :fields="columns"
        responsive
        primary-key="id"
        :sort-by.sync="sortBy"
        show-empty
        :empty-text="empty"
        :sort-desc.sync="isSortDirDesc"
        :filter="filter"
        :empty-filtered-text="empty"
        :current-page="currentPage"
        :per-page="perPage"
      >
        <!-- Column: Actions -->
        <template #cell(actions)="data">
          <b-dropdown
            variant="link"
            no-caret
            :right="$store.state.appConfig.isRTL"
          >

            <template #button-content>
              <feather-icon
                icon="MoreVerticalIcon"
                size="16"
                class="align-middle text-body"
              />
            </template>
            <!-- eslint-disable-next-line -->
            <b-dropdown-item               
              @click="deleteInventoryEntry(data.item.id)"
            >
              <feather-icon icon="Trash2Icon" />
              <span class="align-middle ml-50">Eliminar</span>
            </b-dropdown-item>
          </b-dropdown>
        </template>

      </b-table>
      <div class="mx-2 mb-2">
        <b-row>

          <b-col
            cols="12"
            sm="6"
            class="d-flex align-items-center justify-content-center justify-content-sm-start"
          >
            <!-- <span class="text-muted">Showing {{ dataMeta.from }} to {{ dataMeta.to }} of {{ dataMeta.of }} entries</span> -->
            <span class="text-muted">Total de registros:  {{ rows }} </span>
          </b-col>
          <!-- Pagination -->
          <b-col
            cols="12"
            sm="6"
            class="d-flex align-items-center justify-content-center justify-content-sm-end"
          >

            <b-pagination
              v-model="currentPage"
              :total-rows="rows"
              :per-page="perPage"
              first-number
              last-number
              class="mb-0 mt-1 mt-sm-0"
              prev-class="prev-item"
              next-class="next-item"
            >
              <template #prev-text>
                <feather-icon
                  icon="ChevronLeftIcon"
                  size="18"
                />
              </template>
              <template #next-text>
                <feather-icon
                  icon="ChevronRightIcon"
                  size="18"
                />
              </template>
            </b-pagination>

          </b-col>

        </b-row>
      </div>

      <loading :loading="loading" />

    </b-card>
  </div>
</template>

<script>

/*eslint-disable*/

// Bootstrap
import {
  BCard, BRow, BCol, BFormInput, BButton, BTable, BMedia, BAvatar, BLink,
  BBadge, BDropdown, BDropdownItem, BPagination,
} from 'bootstrap-vue'

// vSelect
import vSelect from 'vue-select'

// Notificaciones
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

// Moment - Fechas
import moment from 'moment'
import 'moment/locale/es'

// Servicios
import * as storeService from '@/services/store/store'

export default {
  components: {
    ToastificationContent,
    BCard,
    BRow,
    BCol,
    BFormInput,
    BButton,
    BTable,
    BMedia,
    BAvatar,
    BLink,
    BBadge,
    BDropdown,
    BDropdownItem,
    BPagination,
    vSelect,
  },
  data() {
    return {
      loading: true,
      items: [],
      dataInventory: {},
      empty: 'No hay datos para mostrar',
      columns: [
        { key: 'code', label: 'Código', sortable: true },
        { key: 'reference', label: 'Referencia', sortable: true },
        { key: 'quantity', label: 'Cantidad', sortable: true },
        { key: 'created_at', label: 'Fecha', sortable: true, formatter: 'moment' },
        { key: 'actions', label: 'Opciones', sortable: true },
      ],
      perPage: 10,
      sortBy: 'id',
      isSortDirDesc: true,
      perPageOptions: [5, 10, 25, 50],
      currentPage: 1,
      filter: '',
    }
  },

  computed: {
    rows() {
      return this.items.length
    },
  },
  
  created() {
    this.getInventoryEntries()
  },

  methods: {
    
    moment(date) {
      return moment(date).format('LLLL');
    },

    async getInventoryEntries() {
      try {
        const response = await storeService.getInventoryEntries()
        if (response.status === 200) {
          this.loading = false
          this.items = response.data.data
          this.moment(this.items.created_at) //Asigna la fecha al metodo moment
        }
      } catch (error) {
        this.loading = false
        this.showToast('Warning', 'AlertCircleIcon', 'No se pudieron obtener los datos', 'warning')
      }
    },

    addInventoryEntries() {
      this.$router.push({ name: 'inventory-entries-add' })
    },

    async deleteInventoryEntry(id) {
      try {

        const modal = await this.$bvModal.msgBoxConfirm('¿Está seguro de eliminar el registro?', {
          okTitle: 'Si',
          okVariant: 'danger',
          cancelTitle: 'No',
          cancelVariant: 'primary',
        })

        if (modal) {
          const response = await storeService.deleteInventoryEntry(id)
          
          if (response.status === 200) {
            this.showToast('Success', 'CheckCircleIcon', 'Registro eliminado correctamente', 'success')
            this.getInventoryEntries()
          }          
        }

      } catch (error) {
        this.showToast('Warning', 'AlertCircleIcon', 'La entrada de inventario no se pudo eliminar', 'warning')
      }
    },

    showToast(title, icon, text, variant) {
      this.$toast({
        component: ToastificationContent,
        props: {
          title,
          icon,
          text,
          variant,
        },
      })
    },

  },

}
</script>

<style lang="scss" scoped>
  .per-page-selector {
    width: 90px;
  }
  @import '@core/scss/vue/libs/vue-select.scss';
</style>
